.fighters {
    display:flex;
    flex-flow: row wrap;
    justify-content: space-around;
    margin-top:-50px;
   
}
.fighters-title h1 {
    color:var(--bgp-very-light-grey, #d4d4d4);
    font-size: 8.5rem;
    font-family: 'FuturaStd-ExtraBoldOblique', var(--font-family-sans-serif,-apple-system, BlinkMacSystemFont, "Segoe UI","Roboto", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol");
    text-transform: uppercase;
}
.fighter-round {
    width:100px;
    text-align: center;
}

.fighter-round__photo {
    img {
        width: 100px;
        height:100px;
        border-radius: 100%;
        border:8px solid var(--bgp-white, #FFFFFF);
        transition-duration: 1s;
        
    }
}

.fighter-round__name {
    a {
        color:inherit;
        text-decoration: none;

    }
    h5 {
        margin: 10px auto 10px auto;
        letter-spacing: -1px;
        
    }
}

.fighter-round__twitter {
    img {
        width:20px;
    }
}

.fighter-round:hover {
    .fighter-round__photo img {
            border:8px solid var(--bgp-black, #000000);
            transition-duration: 1s;
        }
    
}